form {
    display: flex;
    flex-direction: column;
    width: 100%;
    h3 {
        white-space: nowrap; }
    label {
        color: $text65;
        font-size: 0.8rem; }
    input[type=text], input[type=email], textarea {
        font-family: 'Inter', sans-serif;
        color: $text65;
        border: none;
        border-bottom: 1px solid $text25;
        font-size: 1rem;
        line-height: 2rem;
        margin: 0.5rem 0 2rem 0;
        margin-left: 0;
        transition: all .2s ease-in-out; }
    input[type=text]:focus, input[type=email]:focus, textarea:focus {
        color: $text95;
        outline: none;
        border: none;
        border-bottom: 1px solid $text65; }
    button {
        color: $bg;
        background: $text95;
        //background: linear-gradient(90deg, $secondary, $accent)
        font-size: 1rem;
        line-height: 1.5rem;
        padding: 0.75rem 1.15rem;
        border: 1px solid $text95;
        border-radius: 3px;
        width: 40%;
        align-self: center;
        transition: all .1s ease-in-out;
        //animation: gradient 10s ease infinite
        background-size: 200% 200%;
        &:hover {
            //background: linear-gradient(90deg, $accent, $accent)
            background: $bg;
            //border: none
            color: $text95;
            cursor: pointer; } } }
