nav {
    position: fixed;
    width: 84vw;
    height: 10vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background: rgba(255,255,255,1);
    top: 0;
    padding: .3rem;
    z-index: 100;
    @include bp-small {
        width: calc(100% - 36px); }
    .hamburger-close {
        display: none; }
    .hamburger, .hamburger-close {
        cursor: pointer; }
    #home-btn {
        text-decoration: none;
        font-weight: 800;
        font-size: 1.2rem;
        letter-spacing: 2px;
        color: $text35;
        border: none;
        padding: 0;
        &:hover {
            padding: 0;
            color: $text65; }
        //background: linear-gradient(90deg, $secondary, $accent)
        //-webkit-background-clip: text
        //-webkit-text-fill-color: transparent
        //animation: gradient 10s ease infinite
 }        //background-size: 200% 200%
    .menu {
        display: none;
        justify-content: center;
        align-items: center;
        position: fixed;
        width: 100vw;
        height: 90vh;
        overflow: hidden;
        z-index: 1000;
        top: 10vh;
        bottom: 0;
        right: 0;
        left: 0;
        background: $bg;
        ul {
            margin-top: -10vh;
            list-style: none;
            padding-left: 0;
            text-align: center;
            li {
                padding: 1.5rem;
                @include bp-small {
                    padding: 1rem; }
                a {
                    text-decoration: none;
                    font-size: 2.5rem;
                    font-weight: 700;
                    color: $text65;
                    transition: all .1s ease-in-out;
                    border: none;
                    &:hover {
                        color: $text95;
                        border-bottom: 2px solid $text95; }
                    @include bp-small {
                        font-size: 1.7rem; } } } } } }

