//h1, h2, h3, h4
//  font-family: 'Cormorant Garamond', serif
h1 {
    font-size: 9vh;
    font-weight: 600;
    line-height: 1.66;
    margin-bottom: 3vh;
    margin-top: 30vh;
    color: $text95;
    //background: linear-gradient(90deg, $secondary, $accent)
    //-webkit-background-clip: text
    //-webkit-text-fill-color: transparent
    //animation: gradient 10s ease infinite
    //background-size: 200% 200%
    //animation: 1s ease-in-out 0s 1 slideInFromLeft
    @include bp-small {
        font-size: 6.5vh; } }
h2 {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 0;
    padding-top: 11vh; }
h3 {
    font-size: 1.4rem;
    font-weight: 600;
    @include bp-small {
        font-size: 1.2rem; } }
h4 {
    font-size: 1.4rem;
    margin-bottom: 0;
    font-weight: 600; }
p {
    color: $text65;
    line-height: 1.66; }
a {
    color: $secondary;
    transition: all .1s ease-in-out;
    text-decoration: none;
    border-bottom: 1px solid $secondary;
    padding-bottom: 1px;
    &:hover {
        color: $text95;
        padding-bottom: 3px;
        border-bottom: 2px solid $text95; } }
strong {
    font-weight: 700; }
