@keyframes slideInFromLeft {
    0% {
        transform: translateX(-150%); }
    100% {
        transform: translateX(0); } }
@keyframes gradient {
	0% {
		background-position: 0% 50%; }
	50% {
		background-position: 100% 50%; }
	100% {
		background-position: 0% 50%; } }
body {
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    display: flex;
    flex-direction: column;
    padding: 0 8vw;
    color: $text95;
    -webkit-font-smoothing: subpixel-antialiased;
    @include bp-small {
        padding: 0 18px; } }
header {
    width: 60%;
    @include bp-small {
        width: 100%; }
    h3 {
        color: $text65;
        letter-spacing: 1px;
        font-weight: 400;
        line-height: 166%;
        padding-bottom: 10vh; } }
hr {
    color: $text35;
    //background: linear-gradient(90deg, $secondary, $accent)
    //height: 2px
    width: 100%;
    //border: 0
    //animation: gradient 10s ease infinite
 }    //background-size: 200% 200%
.case-studies-text {
    max-width: 75ch;
    margin: 0 auto; }
.case-studies {
    flex-direction: row;
    display: flex;
    @include bp-small {
        flex-direction: column; }
    .case-study {
        text-decoration: none;
        color: inherit;
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        min-height: 30vw;
        text-align: center;
        position: relative;
        margin-bottom: 2rem;
        @include bp-small {
            min-height: 90vw; }
        //p
 }        //  color: white
    a {
        text-decoration: none;
        color: inherit;
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        transition: all .1s ease-in-out;
        //border: 1px solid $bg
        //background: red
        border-bottom: none;
        position: relative;
        &:hover {
            background: $text25;
            //border: 1px solid $text65
            color: $bg; }
        &:hover p {
            color: $bg; }
        &:hover button {
            background: $bg;
            color: $text95;
            box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12); } }
    .coming-soon {
        color: $text35; }
    button {
        color: $bg;
        background: $text95;
        font-size: 0.9rem;
        line-height: 1.2rem;
        padding: 0.6rem 2rem;
        border: 1px solid $text95;
        border-radius: 3px;
        width: auto;
        align-self: center;
        transition: all .1s ease-in-out;
        &:hover {
            background: $text95;
            //border: none
            color: $bg;
            cursor: pointer; } }
    #cc-ux-buddies {
        background: none;
        &:hover {
            background-image: linear-gradient(transparent, $text65, $text95), url("/assets/images/Ux-buddies-sprint-small-min.jpg");
            background-size: cover;
            background-blend-mode: darken; } }
    #cc-irish-radios {
        background: none;
        &:hover {
            background-image: linear-gradient(transparent, $text65, $text95), url("/assets/images/Irish-radios-high-fidelity-small-min.jpg");
            background-size: cover;
            background-blend-mode: darken; } }
    #cc-brandon-f {
        background: none;
        &:hover {
            background-image: linear-gradient(transparent, $text65, $text95), url("/assets/images/Brandon-f-high-fidelity-small-min.jpg");
            background-size: cover;
            background-blend-mode: darken; } } }
.about {
    display: flex;
    flex-direction: row;
    align-items: center;
    picture {
        border-radius: 50%;
        object-fit: cover;
        padding: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex: 1;
        @include bp-small {
            padding: 2rem 0; } }
    @include bp-small {
        flex-direction: column; }
    .text {
        flex: 2;
        padding: 2rem;
        @include bp-small {
            padding: 0 2rem 0 0; } }
    .buttons {
        display: flex;
        flex-direction: row;
        @include bp-small {
            flex-direction: column; } }
    .buttons a {
        color: $bg;
        background: $text95;
        font-size: 0.9rem;
        line-height: 1.2rem;
        padding: 0.6rem 2rem;
        margin: 1rem 2rem 1rem 0;
        border: 1px solid $text95;
        border-radius: 3px;
        width: auto;
        align-self: center;
        transition: all .1s ease-in-out;
        &:hover {
            background: $bg;
            color: $text95;
            border: 1px solid $text95;
            cursor: pointer;
            box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12); } } }
.contact {
    flex-direction: row;
    display: flex;
    @include bp-small {
        flex-direction: column; }
    .contact-form, .contact-list {
        flex: 1;
        display: flex;
        justify-content: top;
        align-items: center;
        flex-direction: column;
        padding: 4rem;
        @include bp-small {
            padding: 2rem 0; }
        ul {
            padding-left: 0;
            li {
                list-style: none;
                line-height: 3; }
            svg {
                margin-top: 1rem; } } } }
.images {
    display: flex;
    justify-content: center;
    flex-direction: column;
    img, source {
        object-fit: cover;
        width: 100%;
        padding-bottom: 1rem; }
    p {
        font-size: 0.9rem;
        font-style: italic;
        margin-top: 0; } }
footer {
    color: $text35;
    padding: 5vh 8vw;
    text-align: center;
    a {
        white-space: pre; }
    @include bp-small {
        padding: 5vh 0; } }
